.hintMarker{
    width: 3rem;
    height: 3rem;
    position: relative;
    border-radius: 90px;

    opacity: 0;
    background-color: rgb(179, 147, 147);
    transition: ease-in-out 0.3s;
}

.hintMarker:hover{
    opacity: 1;
    transition: ease-in-out 0.3s;
    transform: scale(1.3);
}

.hintMarker a{
    width: fit-content;
    height: auto;
}